$color-dark: rgb(50, 31, 155);
$color-gold: #ffd700;
$color-shade-gold: #e9d66b99;
$color-white: #fff;
* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}
html {
	font-size: 62.5%;
}
body {
	background-color: #1c1c1c;
	color: $color-white;
	font-family: 'Roboto', sans-serif;
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
}

.app {
	width: 1400px;
	height: 750px;
	/* From https://css.glass */
	background: rgba(0, 0, 0, 0.2);
	border-radius: 16px;
	box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
	backdrop-filter: blur(3.5px);
	-webkit-backdrop-filter: blur(3.5px);
	border: 1px solid $color-shade-gold;
	display: grid;
	grid-template-columns: 1.5fr 4fr;
	grid-template-rows: repeat(6, 1fr);
	// display: flex;
	// flex-direction: row;

	.searchBar {
		grid-row: 1/2;
		grid-column: 1/2;
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		border-bottom: 1px solid $color-shade-gold;
		// border-right: 2px dotted $color-shade-gold;
		border-right: 2px solid $color-shade-gold;
		background-color: #000;
		border-top-left-radius: 16px;
		&__searchInput {
			font-size: 1.7rem;
			padding: 0.4em 0.6em;
			border-radius: 8px;
			border: none;
			text-transform: uppercase;
		}
		&__searchIcon {
			font-size: 1.7rem;
			padding: 0.5em;
			margin-left: 1em;
			margin-bottom: 0.1em;
			border-radius: 50%;
			border: 1px solid white;
		}
	}

	.list {
		grid-column: 1/2;
		grid-row: 2/8;

		border-bottom-left-radius: 16px;
		// border-right: 2px dotted $color-shade-gold;
		border-right: 2px solid $color-shade-gold;
		background-color: #000;
		font-size: 15px;
		overflow: scroll;
		overflow-x: hidden;
		&::-webkit-scrollbar {
			background: #333;
			width: 12px;
			padding: 10px 4px;
			overflow: hidden;
		}
		&::-webkit-scrollbar-thumb {
			background-color: #111;
			border-radius: 8px;
			margin: 6px 3px;
			outline: 1px solid #232323;
		}

		&__element {
			padding: 1em 2em;
			display: flex;
			align-items: center;
			cursor: pointer;
			&__img {
				width: 50px;
				height: auto;
				pointer-events: none;
			}
			&__name {
				font-weight: 200;
				letter-spacing: 2px;
				text-transform: uppercase;
				margin: 0 1em;
				pointer-events: none;
			}
			&__price {
				font-size: 20px;
				pointer-events: none;
			}
		}
	}
	.actual {
		grid-column: 2/3;
		grid-row: 1/8;
		background-color: #111;
		flex-grow: 1;
		border-top-right-radius: 16px;
		border-bottom-right-radius: 16px;
		display: grid;
		grid-template-rows: 1fr 0.6fr 1.2fr;
		grid-template-columns: 1fr 1fr 1fr 1fr;
		padding: 2em 1em;
		font-size: 2rem;
		&__logo {
			grid-row: 1/3;
			grid-column: 1/2;
			display: flex;
			flex-direction: column;
			align-items: center;

			&__img {
				width: 50%;
				height: auto;
				margin-bottom: 1.5em;
			}
			&__name {
				font-size: 3rem;
				font-weight: 500;
				letter-spacing: 1px;
			}
		}
		&__current {
			display: flex;

			letter-spacing: 1px;
			text-align: right;
			flex-direction: column;
			h3 {
				font-weight: 500;
				margin-bottom: 0.5em;
			}
			p {
				font-size: 3rem;
			}
		}
		&__change {
			font-weight: 400;
			text-align: right;
			&__data {
				display: flex;
				align-items: center;
				justify-content: end;
			}
			i {
				font-size: 3rem;
				padding: 0.2em 0.4em;
				margin-right: 0.2em;
			}
			h3 {
				font-weight: 500;
				margin-bottom: 0.5em;
			}
			p {
				padding: 0.25em 0;
			}
		}
		&__currencyChanger {
			position: relative;
			align-self: start;
			justify-self: center;
			display: flex;
			flex-direction: column;
			align-items: center;
			width: 38px;
			height: 70px;
			background-color: $color-shade-gold;
			border-radius: 25px;
			grid-row: 1/2;
			grid-column: 4/5;
			cursor: pointer;
			&__circle {
				position: absolute;
				bottom: 5px;
				top: none;
				width: 32px;
				height: 32px;
				background-color: #111;
				border-radius: 25px;
				pointer-events: none;
				&::before {
					content: '';
				}
			}
			&__USD {
				position: absolute;
				left: 150%;
				bottom: 0;
			}
			&__PLN {
				position: absolute;
				left: 150%;
				top: 0;
			}
		}
		&__dateChanger {
			grid-column: 3/5;
			grid-row: 2/3;
			display: flex;
			flex-direction: row;
			align-items: flex-end;
			justify-content: center;
			&__changeBtn {
				font-size: 1.5rem;
				padding: 0.5em 1em;
				text-transform: uppercase;
				background-color: #333;
				border: none;
				color: #fff;
				cursor: pointer;				
				box-shadow: 0px 0px -5px #444;
				&--active{
					background-color: #222;
					box-shadow: 0px 0px 5px #333;
				}
			}
		}

		&__graph {
			grid-column: 1/5;
			grid-row: 3/4;
			width: 100%;
			align-self: center;
			display: flex;
			align-items: center;
			justify-content: center;
			height: 100%;
			canvas {
				padding: 2em 1em;
			}
		}
	}
	.welcomeScreen {
		grid-column: 2/3;
		grid-row: 1/8;
		flex-grow: 1;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		text-align: center;
		h2 {
			font-size: 4rem;
		}
		p {
			font-size: 2.5rem;
		}
	}
}

.moveUp {
	top: 5px;
	bottom: none;
}
